body{
    background-image: url("../Resources/bgd.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
}
.name-container{
    text-align: left;
    padding-top: 40px;
}
.name{
    color: white;
    font-weight: bolder;
    font-size: 55px;
    padding-left: 90px;
}
.text-container{
    text-align: left;
    padding-top: 20px;
    width: 600px;
    padding-left: 90px;
}
.p-text{
    color: #61dafb;
    font-size: 20px;
}
@media only screen
and (min-device-width: 375px)
and (max-device-width: 812px)
and (-webkit-min-device-pixel-ratio: 3) {
    .name-container{
        text-align: left;
        padding-top: 40px;
    }
    .name{
        color: white;
        font-weight: bolder;
        font-size: 35px;
        padding-left: 40px;
    }
    .text-container{
        text-align: left;
        padding-top: 20px;
        width: 400px;
        padding-left: 40px;
    }
    .p-text{
        color: #61dafb;
        font-size: 15px;
    }
}
